:root {
    /* Color Palette - Clean & Chill */
    --color-bg-primary: #FAFAF9; /* Stone 50 */
    --color-bg-secondary: #FFFFFF; /* White */
    
    --color-text-primary: #1F2937; /* Gray 800 */
    --color-text-secondary: #6B7280; /* Gray 500 */
    --color-text-inverted: #FFFFFF;

    /* Accents */
    /* Green - Chill Emerald */
    --color-accent-green: #34D399; /* Emerald 400 */
    --color-accent-green-hover: #10B981; /* Emerald 500 */
    --color-accent-green-light: #D1FAE5; /* Emerald 100 */

    /* Red - Soft Red */
    --color-accent-red: #F87171; /* Red 400 */
    --color-accent-red-hover: #EF4444; /* Red 500 */
    --color-accent-red-light: #FEE2E2; /* Red 100 */

    /* UI Elements */
    --color-border: #E5E7EB; /* Gray 200 */
    --shadow-soft: 0 4px 6px -1px rgba(0, 0, 0, 0.05), 0 2px 4px -1px rgba(0, 0, 0, 0.03);
    --shadow-hover: 0 10px 15px -3px rgba(0, 0, 0, 0.05), 0 4px 6px -2px rgba(0, 0, 0, 0.03);
    
    --radius-sm: 0.375rem;
    --radius-md: 0.5rem;
    --radius-lg: 1rem;
    --radius-xl: 1.5rem;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
    background-color: var(--color-bg-primary);
    color: var(--color-text-primary);
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Outfit', sans-serif;
    font-weight: 700;
    color: var(--color-text-primary);
}

a {
    text-decoration: none;
    color: inherit;
    transition: opacity 0.2s ease;
}

button {
    cursor: pointer;
    font-family: 'Outfit', sans-serif;
}

/* Utilities */
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
}`, "",{"version":3,"sources":["webpack://./index.css"],"names":[],"mappings":"AAAA,wBAAwB;;AAGxB;IACI,kCAAkC;IAClC,2BAA2B,EAAE,aAAa;IAC1C,6BAA6B,EAAE,UAAU;;IAEzC,6BAA6B,EAAE,aAAa;IAC5C,+BAA+B,EAAE,aAAa;IAC9C,8BAA8B;;IAE9B,YAAY;IACZ,0BAA0B;IAC1B,6BAA6B,EAAE,gBAAgB;IAC/C,mCAAmC,EAAE,gBAAgB;IACrD,mCAAmC,EAAE,gBAAgB;;IAErD,mBAAmB;IACnB,2BAA2B,EAAE,YAAY;IACzC,iCAAiC,EAAE,YAAY;IAC/C,iCAAiC,EAAE,YAAY;;IAE/C,gBAAgB;IAChB,uBAAuB,EAAE,aAAa;IACtC,qFAAqF;IACrF,wFAAwF;;IAExF,qBAAqB;IACrB,mBAAmB;IACnB,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,SAAS;IACT,UAAU;IACV,SAAS;IACT,sBAAsB;AAC1B;;AAEA;IACI,gCAAgC;IAChC,yCAAyC;IACzC,gCAAgC;IAChC,gBAAgB;IAChB,mCAAmC;IACnC,kCAAkC;IAClC,kBAAkB;AACtB;;AAEA;IACI,iCAAiC;IACjC,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA;IACI,qBAAqB;IACrB,cAAc;IACd,6BAA6B;AACjC;;AAEA;IACI,eAAe;IACf,iCAAiC;AACrC;;AAEA,cAAc;AACd;IACI,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,UAAU;IACV,YAAY;IACZ,gBAAgB;IAChB,sBAAsB;IACtB,mBAAmB;IACnB,eAAe;AACnB","sourcesContent":["/* Google Fonts Import */\n@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&family=Outfit:wght@500;700&display=swap');\n\n:root {\n    /* Color Palette - Clean & Chill */\n    --color-bg-primary: #FAFAF9; /* Stone 50 */\n    --color-bg-secondary: #FFFFFF; /* White */\n    \n    --color-text-primary: #1F2937; /* Gray 800 */\n    --color-text-secondary: #6B7280; /* Gray 500 */\n    --color-text-inverted: #FFFFFF;\n\n    /* Accents */\n    /* Green - Chill Emerald */\n    --color-accent-green: #34D399; /* Emerald 400 */\n    --color-accent-green-hover: #10B981; /* Emerald 500 */\n    --color-accent-green-light: #D1FAE5; /* Emerald 100 */\n\n    /* Red - Soft Red */\n    --color-accent-red: #F87171; /* Red 400 */\n    --color-accent-red-hover: #EF4444; /* Red 500 */\n    --color-accent-red-light: #FEE2E2; /* Red 100 */\n\n    /* UI Elements */\n    --color-border: #E5E7EB; /* Gray 200 */\n    --shadow-soft: 0 4px 6px -1px rgba(0, 0, 0, 0.05), 0 2px 4px -1px rgba(0, 0, 0, 0.03);\n    --shadow-hover: 0 10px 15px