___CSS_LOADER_EXPORT___.push([module.id, `/* accent options */
:root {
    --accent-color: var(--color-accent);
    --accent-width: 2px;
}

/* prevent layout disorder in safari */
ruby > rt {
    height: 1em;
}

/* accent definition */
[class*='accent'] {        
    /* height matching w/ rt */
    padding-top: .5em !important;

    /* no need for rt */
    &:is(.main ruby > rt [class*='accent']) {
        padding-top: 0 !important; 
    }

    border: 
        0
        solid 
        var(--accent-color) !important;
    border-top-width:  
        var(--accent-width) !important;
}    

/* trailing drop for kana */
.accent-drop::after {
    /* apply invisible volume */
    content: '';
    display: inline-block;
    color: transparent;
    pointer-events: none;
    
    /* match the height of rt's and their borders */
    height: 
        calc(
            .5em + 
            2 * var(--accent-width)
        );
    
    /* match the height of its parent */
    translate: 
        0 
        calc(
            -1em - 
            -2 * var(--accent-width)
        );
    
    border-left: 
        solid 
        var(--accent-color) 
        var(--accent-width);
}

/* trailing drop for furikana */
rt .accent-drop {
    border-right-width: var(--accent-width) !important;
    
    /* avoid redundant lines */
    &::after {
        display: none;
    }
}

/* prevent trailing drop sticking to the right */
.accent-drop:last-child {
    margin-right: 1px;
}`, "",{"version":3,"sources":["webpack://./utilities/accentMarker.css"],"names":[],"mappings":"AAAA,mBAAmB;AACnB;IACI,mCAAmC;IACnC,mBAAmB;AACvB;;AAEA,sCAAsC;AACtC;IACI,WAAW;AACf;;AAEA,sBAAsB;AACtB;IACI,0BAA0B;IAC1B,4BAA4B;;IAE5B,mBAAmB;IACnB;QACI,yBAAyB;IAC7B;;IAEA;;;sCAGkC;IAClC;sCACkC;AACtC;;AAEA,2BAA2B;AAC3B;IACI,2BAA2B;IAC3B,WAAW;IACX,qBAAqB;IACrB,kBAAkB;IAClB,oBAAoB;;IAEpB,+CAA+C;IAC/C;;;;SAIK;;IAEL,mCAAmC;IACnC;;;;;SAKK;;IAEL;;;2BAGuB;AAC3B;;AAEA,+BAA+B;AAC/B;IACI,kDAAkD;;IAElD,0BAA0B;IAC1B;QACI,aAAa;IACjB;AACJ;;AAEA,gDAAgD;AAChD;IACI,iBAAiB;AACrB","sourcesContent":["/* accent options */\n:root {\n    --accent-color: var(--color-accent);\n    --accent-width: 2px;\n}\n\n/* prevent layout disorder in safari */\nruby > rt {\n    height: 1em;\n}\n\n/* accent definition */\n[class*='accent'] {        \n    /* height matching w/ rt */\n    padding-top: .5em !important;\n\n    /* no need for rt */\n    &:is(.main ruby > rt [class*='accent']) {\n        padding-top: 0 !important; \n    }\n\n    border: \n        0\n        solid \n        var(--accent-color) !important;\n    border-top-width:  \n        var(--accent-width) !important;\n}    \n\n/* trailing drop for kana */\n.accent-drop::after {\n    /* apply invisible volume */\n    content: '';\n    display: inline-block;\n    color: transparent;\n    pointer-events: none;\n    \n    /* match the height of rt's and their borders */\n    height: \n        calc(\n            .5em + \n            2 * var(--accent-width)\n        );\n    \n    /* match the height of its parent */\n    translate: \n        0 \n        calc(\n            -1em - \n            -2 * var(--accent-width)\n        );\n    \n    border-left: \n        solid \n        var(--accent-color) \n        var(--accent-width);\n}\n\n/* trailing drop for furikana */\nrt .accent-drop {\n    border-right-width: var(--accent-width) !important;\n    \n    /* avoid redundant lines */\n    &::after {\n        display: none;\n    }\n}\n\n/* prevent trailing drop