    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    width: 100%;
    padding: 1rem;
}

.skeleton-line {
    height: 1.25rem;
    background-color: #E5E7EB; /* Gray 200 */
    border-radius: 0.375rem;
    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

