___CSS_LOADER_EXPORT___.push([module.id, `.run-container {
    margin-top: 1rem;
    display: flex;
    justify-content: flex-end;
}

.run-button {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1.5rem;
    background-color: var(--color-accent-green);
    color: white;
    font-weight: 600;
    border-radius: var(--radius-md);
    transition: all 0.2s ease;
    border: none;
    font-size: 1rem;
    box-shadow: 0 4px 6px -1px rgba(52, 211, 153, 0.4);
}

.run-button:hover:not(:disabled) {
    background-color: var(--color-accent-green-hover);
    transform: translateY(-1px);
    box-shadow: 0 6px 8px -1px rgba(52, 211, 153, 0.5);
}

.run-button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    background-color: var(--color-text-secondary);
    box-shadow: none;
}

.run-button .icon {
    width: 1.25rem;
    height: 1.25rem;
}

.run-button .animate-spin {
    animation: spin 1s linear infinite;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}`, "",{"version":3,"sources":["webpack://./components/Run.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,uBAAuB;IACvB,2CAA2C;IAC3C,YAAY;IACZ,gBAAgB;IAChB,+BAA+B;IAC/B,yBAAyB;IACzB,YAAY;IACZ,eAAe;IACf,kDAAkD;AACtD;;AAEA;IACI,iDAAiD;IACjD,2BAA2B;IAC3B,kDAAkD;AACtD;;AAEA;IACI,YAAY;IACZ,mBAAmB;IACnB,6CAA6C;IAC7C,gBAAgB;AACpB;;AAEA;IACI,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI;QACI,uBAAuB;IAC3B;IACA;QACI,yBAAyB;IAC7B;AACJ","sourcesContent":[".run-container {\n    margin-top: 1rem;\n    display: flex;\n    justify-content: flex-end;\n}\n\n.run-button {\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n    padding: 0.75rem 1.5rem;\n    background-color: var(--color-accent-green);\n    color: white;\n    font-weight: 600;\n    border-radius: var(--radius-md);\n    transition: all 0.2s ease;\n    border: none;\n    font-size: 1rem;\n    box-shadow: 0 4px 6px -1px rgba(52, 211, 153, 0.4);\n}\n\n.run-button:hover:not(:disabled) {\n    background-color: var(--color-accent-green-hover);\n    transform: translateY(-1px);\n    box-shadow: 0 6px 8px -1px rgba(52, 211, 153, 0.5);\n}\n\n.run-button:disabled {\n    opacity: 0.6;\n    cursor: not-allowed;\n    background-color: var(--color-text-secondary);\n    box-shadow: none;\n}\n\n.run-button .icon {\n    width: 1.25rem;\n    height: 1.25rem;\n}\n\n.run-button .animate-spin {\n    animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n    from {\n        transform: rotate(0deg);\n    }\n    to {\n        transform: rotate(360deg);\n    }\n}"],"sourceRoot":""}]);
