    width: 100%;
    height: 4rem;
    display: flex;
    align-items: center;
    padding: 0 2rem;
    background-color: var(--color-bg-secondary);
    border-bottom: 1px solid var(--color-border);
    position: sticky;
    top: 0;
    z-index: 100;
}

.nav .nav-title {
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 0.75rem;
    color: var(--color-text-primary);
    transition: opacity 0.2s;
}

.nav .nav-title:hover {
    opacity: 0.8;
}

.nav .logo {
    width: 2rem;
    height: 2rem;
    object-fit: contain;
}

.nav .title {
    font-family: 'Outfit', sans-serif;
    font-weight: 700;
    font-size: 1.25rem;
    color: var(--color-text-primary);
