___CSS_LOADER_EXPORT___.push([module.id, `.result-container-inner {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.result-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--color-border);
}

.result-title {
    font-size: 1.25rem;
    color: var(--color-text-primary);
    margin: 0;
}

.result-actions {
    display: flex;
    gap: 0.5rem;
}

.action-button {
    background-color: transparent;
    border: none;
    color: var(--color-text-secondary);
    padding: 0.5rem;
    border-radius: var(--radius-md);
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.action-button:hover {
    background-color: var(--color-bg-primary);
    color: var(--color-text-primary);
}

.theme-toggle.active {
    color: var(--color-accent-green);
    background-color: var(--color-accent-green-light);
}

.result-content {
    flex: 1;
    overflow-y: auto;
    position: relative;
    padding: 0.5rem;
}

.empty-state {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: var(--color-text-secondary);
    font-style: italic;
}

.result-area {
    font-size: 1.5rem;
    line-height: 2;
    color: var(--color-text-primary);
}

/* Ruby Styling */
ruby {
    display: inline-flex;
    flex-direction: column-reverse;
    vertical-align: bottom;
    align-items: center;
    margin: 0 0.2em;
}

rt {
    font-size: 0.6em;
    line-height: 1.2;
    user-select: none;
    margin-bottom: 0.2em;
}

.dark-theme .result-area {
    background-color: #1F2937;
    color: #F9FAFB;
    padding: 1rem;
    border-radius: var(--radius-md);
}

.toast-notification {
    position: absolute;
    top: 4rem;
    right: 1rem;
    background-color: var(--color-accent-green);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: var(--radius-md);
    font-size: 0.875rem;
    box-shadow: var(--shadow-hover);
    animation: fadeInOut 2s ease forwards;
    z-index: 10;
}

@keyframes fadeInOut {
    0% { opacity: 0; transform: translateY(-10px); }
    10% { opacity: 1; transform: translateY(0); }
    90% { opacity: 1; transform: translateY(0); }
    100% { opacity: 0; transform: translateY(-10px); }
